import Table from '../../mo/models';
import TableCell from '../../mo/models/tableCell';
import { logNPEInfo } from './logger';

export default function traverseSingleRowCells(
  table: Table,
  targetRowIndex: number,
  triggerRowIndex: number,
  callback: (
    targetRowIndex: number,
    colIndex: number,
    triggerCell: TableCell,
    originalCell: TableCell | null,
  ) => void,
) {
  const targetRow = table.nodes[targetRowIndex];
  const triggerRow = table.nodes[triggerRowIndex];
  // triggerRow 可能被协作者删除
  if (!triggerRow) {
    logNPEInfo(
      table,
      {
        type: 'traverseSingleRowCells',
        rowIndex: triggerRowIndex,
      },
    );
    return;
  }
  for (let colIndex = 0; colIndex < triggerRow.nodes.length; colIndex += 1) {
    const triggerCell = triggerRow.nodes[colIndex];
    const originalCell = targetRow ? targetRow.nodes[colIndex] : null;
    callback(targetRowIndex, colIndex, triggerCell, originalCell);
  }
}
